import { Router } from '@angular/router';
import { DataService } from '../data.service';
import { HttpEventType } from '@angular/common/http';
var URL = './api/upload';
var UploadComponent = /** @class */ (function () {
    function UploadComponent(router, dataService) {
        var _this = this;
        this.router = router;
        this.dataService = dataService;
        this.subjects = {};
        this.model = {};
        this.selectedFile = null;
        this.progress = null;
        // declare a property called fileuploader and assign it to an instance of a new fileUploader.
        // pass in the Url to be uploaded to, and pass the itemAlais, which would be the name of the //file input when sending the post request.
        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.dataService.getSubjects().subscribe(function (data) { _this.subjects = data; console.log(_this.subjects); });
    }
    UploadComponent.prototype.ngOnInit = function () { };
    UploadComponent.prototype.onUpload = function () {
        var _this = this;
        if (this.selectedFile != null) {
            var formData = new FormData();
            formData.append('title', this.model.title);
            formData.append('desc', this.model.desc);
            formData.append('course', this.model.course);
            formData.append('type', 'file');
            formData.append('file', this.selectedFile);
            this.dataService.upload(formData).subscribe(function (event) {
                if (event.type === HttpEventType.UploadProgress) {
                    _this.progress = Math.round(event.loaded / event.total * 100);
                }
                else if (event.type === HttpEventType.Response) {
                    if (event.status === 200) {
                        //
                        alert('Success');
                    }
                }
                console.log(event);
            });
        }
    };
    UploadComponent.prototype.register = function () {
        this.router.navigate(['register']);
    };
    UploadComponent.prototype.onFileSelected = function (event) {
        console.log(event.target.files[0]);
        this.selectedFile = event.target.files[0];
    };
    return UploadComponent;
}());
export { UploadComponent };



