import { Component, OnInit, ViewChild } from '@angular/core';
import { DataService } from '../data.service';
import { HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';
import { RouterLink } from '@angular/router';
import { FormGroup, FormControl, ReactiveFormsModule } from '@angular/forms';
import { Validators } from '@angular/forms';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})

export class RegisterComponent implements OnInit {
  myForm: FormGroup;
  name: FormControl;
  card: FormControl;
  sno: FormControl;
  tpno: FormControl;
  email: FormControl;
  password: FormControl;

  @ViewChild('fileInput') fileInput;

  visibleTab: Number = 1;
  private uploading = false;
  private suffix = '';
  selectedFile = null;
  progress = null;
  constructor(private dataService: DataService, private router: Router) { }

  ngOnInit() {
    this.createFormControls();
    this.createForm();
  }
  createFormControls() {
    this.name = new FormControl('', Validators.required);
    this.card = new FormControl('', Validators.required);
    this.sno = new FormControl('', [Validators.required, Validators.pattern('[Ss][/][0-9]{2}[/][0-9]{3}')]);
    this.tpno = new FormControl('', [Validators.required, Validators.pattern('(07)\d{8}')]);
    this.email = new FormControl('', [Validators.required, Validators.pattern('[^ @]*@[^ @]*')]);
    this.password = new FormControl('', [Validators.minLength(6), Validators.required]);
  }

  createForm() {
    this.myForm = new FormGroup({
    name: this.name,
    card: this.card,
    sno: this.sno,
    tpno: this.tpno,
    email: this.email,
    password: this.password
    });
  }

  upload() {
    this.uploading = true;
    this.suffix = 'ing';

  }

  onUpload() {
    if (this.selectedFile != null) {
      const formData = new FormData();
      formData.append('name', this.myForm.value.name);
      formData.append('card', this.myForm.value.card);
      formData.append('sno', this.myForm.value.sno);
      formData.append('tpno', this.myForm.value.tpno);
      formData.append('email', this.myForm.value.email);
      formData.append('password', this.myForm.value.password);
      formData.append('image', this.selectedFile);
      this.dataService.create(formData).subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {
          this.progress = Math.round(event.loaded / event.total * 100);
        } else if (event.type === HttpEventType.Response) {
          if (event.status === 200) {
            this.visibleTab = 200;
          }
        }
        console.log(event);
      });
    }
  }


  register() {
    this.visibleTab = 2;
  }

  onFileSelected(event) {
    // console.log(event.target.files[0]);
    this.selectedFile = event.target.files[0];
  }
}



