import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../data.service';
import { HttpEventType } from '@angular/common/http';
import { FormGroup } from '@angular/forms/src/model';

const URL = './api/upload';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  subjects: any = {};
  model: any = {};
  selectedFile = null;
  progress = null;

  constructor(private router: Router, private dataService: DataService) {
    this.dataService.getSubjects().subscribe(data => { this.subjects = data; console.log(this.subjects); });
  }
  // declare a property called fileuploader and assign it to an instance of a new fileUploader.
  // pass in the Url to be uploaded to, and pass the itemAlais, which would be the name of the //file input when sending the post request.
  private currentUser = JSON.parse(localStorage.getItem('currentUser'));

  ngOnInit() {}

  onUpload() {
    if (this.selectedFile != null) {
      const formData = new FormData();
      formData.append('title', this.model.title);
      formData.append('desc', this.model.desc);
      formData.append('course', this.model.course);
      formData.append('type', 'file');
      formData.append('file', this.selectedFile);
      this.dataService.upload(formData).subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {
          this.progress = Math.round(event.loaded / event.total * 100);
        } else if (event.type === HttpEventType.Response) {
          if (event.status === 200) {
            //
            alert('Success');
          }
        }
        console.log(event);
      });
    }
  }

  register() {
    this.router.navigate(['register']);
  }

  onFileSelected(event) {
    console.log(event.target.files[0]);
    this.selectedFile = event.target.files[0];
  }
}



