import { DataService } from '../data.service';
import { HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
import { Validators } from '@angular/forms';
var RegisterComponent = /** @class */ (function () {
    function RegisterComponent(dataService, router) {
        this.dataService = dataService;
        this.router = router;
        this.visibleTab = 1;
        this.uploading = false;
        this.suffix = '';
        this.selectedFile = null;
        this.progress = null;
    }
    RegisterComponent.prototype.ngOnInit = function () {
        this.createFormControls();
        this.createForm();
    };
    RegisterComponent.prototype.createFormControls = function () {
        this.name = new FormControl('', Validators.required);
        this.card = new FormControl('', Validators.required);
        this.sno = new FormControl('', [Validators.required, Validators.pattern('[Ss][/][0-9]{2}[/][0-9]{3}')]);
        this.tpno = new FormControl('', [Validators.required, Validators.pattern('(07)\d{8}')]);
        this.email = new FormControl('', [Validators.required, Validators.pattern('[^ @]*@[^ @]*')]);
        this.password = new FormControl('', [Validators.minLength(6), Validators.required]);
    };
    RegisterComponent.prototype.createForm = function () {
        this.myForm = new FormGroup({
            name: this.name,
            card: this.card,
            sno: this.sno,
            tpno: this.tpno,
            email: this.email,
            password: this.password
        });
    };
    RegisterComponent.prototype.upload = function () {
        this.uploading = true;
        this.suffix = 'ing';
    };
    RegisterComponent.prototype.onUpload = function () {
        var _this = this;
        if (this.selectedFile != null) {
            var formData = new FormData();
            formData.append('name', this.myForm.value.name);
            formData.append('card', this.myForm.value.card);
            formData.append('sno', this.myForm.value.sno);
            formData.append('tpno', this.myForm.value.tpno);
            formData.append('email', this.myForm.value.email);
            formData.append('password', this.myForm.value.password);
            formData.append('image', this.selectedFile);
            this.dataService.create(formData).subscribe(function (event) {
                if (event.type === HttpEventType.UploadProgress) {
                    _this.progress = Math.round(event.loaded / event.total * 100);
                }
                else if (event.type === HttpEventType.Response) {
                    if (event.status === 200) {
                        _this.visibleTab = 200;
                    }
                }
                console.log(event);
            });
        }
    };
    RegisterComponent.prototype.register = function () {
        this.visibleTab = 2;
    };
    RegisterComponent.prototype.onFileSelected = function (event) {
        // console.log(event.target.files[0]);
        this.selectedFile = event.target.files[0];
    };
    return RegisterComponent;
}());
export { RegisterComponent };



